<template>
    <div>
        <div class="body">
            <router-link to='index'>
            <img src="img/login/logo.jpg" alt="">
            </router-link>
            <span></span>
            <div class="first" v-show="f1==true">
                <form action="">
                    <div class="area">
                        <p>/</p>
                        <input type="text" placeholder="" v-model="chosed">
                        <div>
                            <span class="rotate mui-icon-extra mui-icon-extra-arrowrightcricle"  @click="change" :style="rotate"></span>
                            <div class="area-drop" :style="dis">
                                <input type="text" v-model="sous">
                                <ul class="u1">
                                    <li v-for="item of lists" @click="chose" :data-name="item.name">{{item.name}}</li>
                                </ul>
                            </div>
                        </div>
                        <p>,/</p>
                    </div>
                    

                    <div class="phone">
                        <p></p>
                        <div>+86</div>
                        <input type="number" placeholder="" v-model="phone" @blur="number">
                    </div>
                    <span class="mui-icon mui-icon-info-filled ap" v-show="f4==false">{{tishi}}</span>
                </form>
                     <button @click="login" class="zhuce"></button>
                <div class="contract">
                    <input type="checkbox" v-model="ischecked">
                    <span><span></span><span></span></span>
                </div>
            </div>

            <div class="second" v-show="f2==false">
                <span><span>+86&nbsp;{{phone}}</span></span>
                <span></span>
                <div>
                    <input type="number" maxlength="6" placeholder=""> <button></button>
                </div>
                <button @click="next"></button>
                <button @click="back"></button>
            </div>

            <div class="third" v-show="f3==false">
                <span></span>
                <input type="text" placeholder="" v-model="sign_name" @blur="name">
                <p class="ap" v-show="f5">{{sign_name_tishi}}</p>
                <span></span>
                <input type="password" placeholder="" v-model="sign_pwd" @blur="pwd"> 
                <p class="ap" v-show="f6">!</p>
                <span></span>
                <input type="password" placeholder="" v-model="sign_pwd1" @blur="pwd1">
                <p class="ap" v-show="f7">,!</p>
                <el-button :plain="true" @click="loginon"></el-button>
            </div>

        </div>
    <!--  -->
        <div class="boot">
            <span></span>
            <span></span>
            <span>English</span>
            <span></span>
            <span></span>
            <p>-ICP10046444-<img src="img/login/ghs.png" alt="">11010802020134-ICP110457</p>
        </div>
    </div>
</template>
<script>

export default {
    data(){
        return{
            ischecked:false,
            f1:true,
            f2:true,
            f3:true,
            f4:true,
            f5:false,
            f6:false,
            f7:false, 
            sign_name:"", //
            sign_name_num:"",
            sign_pwd:"", //
            sign_pwd1:"", //
            rotate:"",  //
            chosed:"",
            sign_name_tishi:"!",
            tishi:"",
            phone:"",
            dis:"display:none",
            sous:"",
            lists:[],
            list:[
                {id:1,name:""},
                {id:2,name:""},
                {id:3,name:""},
                {id:4,name:""},
                {id:5,name:""},
                {id:6,name:""},
                {id:7,name:""},
                {id:8,name:""},
                {id:9,name:""},
                {id:10,name:""},
                {id:11,name:""},
                {id:12,name:""},
                {id:13,name:""}
            ]
        }
    },
    methods:{
        change(){ 
            if(this.dis=="display:none"){ //,
                this.dis="dispaly:block"  
                this.lists=this.list
                this.rotate="transform:rotate(-90deg)"
            }else{
                this.dis="display:none"
                this.rotate="transform:rotate(90deg)"
            }
        },
        chose(e){
            var name=e.target.dataset.name  //
            this.chosed=name            //
            this.dis="display:none"     //
            this.sous=""             //
        },
        number(){
            var reg=/^1[3-9]\d{9}$/
            if(this.phone==""){
                this.tishi=""
                this.f4=false
            }else if(!reg.test(this.phone)){
                this.tishi=""
                this.f4=false
            }
        },
        login(){
            var reg=/^1[3-9]\d{9}$/
            if(this.ischecked==false){
                this.f4=false
            }else if(this.phone=="" || !reg.test(this.phone)){
             this.phone=="" ? this.tishi="" : this.tishi=""
                this.f4=false
            }else{
                this.f1=false
                this.f2=false
            }
        },
        back(){
             this.f1=true
            this.f2=true
        },
        name(){//
           
        },
        pwd(){//
            if(!this.sign_pwd){
                this.f6=true
                return
            }else{
                this.f6=false
            }
        },
        pwd1(){//
            if(this.sign_pwd!==this.sign_pwd1){
                this.f7=true
                return
            }else{
                this.f7=false
            }
        },
        next(){
            this.f2=true
            this.f3=false
        },
        loginon(){ //
            if(this.sign_name!=="" && this.sign_name_num==1 && this.sign_pwd!=="" && this.sign_pwd==this.sign_pwd1){
                var url="sign_in"
                var formdata="name="+this.sign_name+"&pwd="+this.sign_pwd+"&phone="+this.phone
                //
                var ming=this.sign_name
                var mima=this.sign_pwd
                this.axios.post(url,formdata).then(res=>{
                    if(res.data.code==1){
                        //
                        this.sign_name=""
                        this.sign_pwd=""
                        this.sign_pwd1=""

                        this.$message({
                            message: ',5S',
                            type: 'success'
                            });
                        setTimeout(()=>{
                            var url="login";
                            var formdata="name="+ming+"&pwd="+mima;
                            this.axios.post(url,formdata).then(res=>{
                            if(res.data.code==1){
                                var name=res.data.data[0].uname
                                var uid=res.data.data[0].uid
                                this.$store.commit('changeName',name) //
                                this.$store.commit('changeUid',uid) //id
                                this.$router.push("/index")
                                }
                            })
                        },5000)
                    }
                })
            }
        }
    },
    watch:{
        sous(){
            if(this.sous!=""){
                var arr=[]  //
                for(var key of this.list){
                    if(key.name.indexOf(this.sous)!=-1){//
                        arr.push(key)   
                    }
                }
                this.lists=arr  //
            }
            if(this.sous==""){
                this.lists=this.list  //
            }
        },
        ischecked(){
            if(this.ischecked){
                this.f4=true
            }
        },
        sign_name(){
            if(!this.sign_name){
                this.sign_name_tishi="!"
                this.f5=true
                return
            }else{
                var url="search"
               var formdata="name="+this.sign_name
                this.axios.post(url,formdata).then(res=>{
                    var code=res.data.code
                    this.sign_name_num=code
                    this.f5=true
                    if(code==-1){
                        this.sign_name_tishi=""
                    }else{
                        this.sign_name_tishi=""  
                    }
                })
               
            }
        }
    }
}
</script>
<style scoped>
    *{margin:0;padding:0}
    .body{
        width:800px;
        height:600px;
        margin:100px auto 0;
        background:#fff;
        display:flex;
        justify-content:flex-start;
        align-items:center;
        flex-direction:column;
        font-size:13px;
    }
    .body img{
        width:80px;
    }
     .body img+span{
         font-size:30px;
         padding:45px 0;
     }
    .area,.phone{
        margin:10px 0;
    }
    .area span{
        transform:rotate(90deg)
    }
    .area input{
        width:300px;height:45px;
        border-radius:0;
        margin:0;
        vertical-align:bottom;
    }
    .area>div{
        width:45px;height:45px;
        border:1px solid #ccc;
        display:inline-block;
        position:relative;
        text-align:center;
        line-height:56px;
        border-left:0;
    }
    .phone>div{
        width:45px;height:45px;
        border:1px solid #ccc;
        display:inline-block;
        position:relative;
        text-align:center;
        line-height:45px;
        border-right:0;
    }
    .phone input{
        width:300px;height:45px;
        border-radius:0;
        margin:0;
        vertical-align:bottom;
    }
    .ap{
        color:#ff7600;
        font-size:15px;
        margin:0px 0 10px 0 ;
    }
    .rotate{
        transition:all 0.3s linear;
    }

    .zhuce{
        display:block;
        width:345px;
        height:45px;
        margin:0px 0 20px 0;
        background:#ff7655;
        color:#fff;
    }
    .contract input{
        width:15px;
        height:15px;
        float:left;
        margin-top:3px;
    }
/* */
    .area-drop{
        position:absolute;
        top:0;right:-1px;
        margin-top:44px;
        z-index:1;
        border:1px solid #aaa;
        background:#fff;
        border-top:0;
        height:300px;
        overflow:auto;
        width:345px;       
    }
    .area-drop ul{
        list-style:none;
        width:325px;     
    }
     .area-drop ul li{
         border-bottom:1px solid #aaa;
         height:40px;
         line-height:40px;
         font-size:15px;
     }
     .contract>span>span{
         color:#ff7600
     }
/* */
    .boot{
        background:#fff;
        text-align:center;
        font-size:15px;
        color:#aaa;
        padding:83px 0;
    }
    .boot span+span:before{
        display:inline-block;
        content:"|";
        margin:0 10px;
    }
    .boot img{
        vertical-align:baseline;
        margin-top:5px;
    }
    
    .boot span:hover{
        color:#000;
        cursor:pointer;
    }
/***/
    .second{
        display:flex;
        justify-content:flex-start;
        flex-direction:column;
        width:350px;
        color:#777;
        font-size:15px;
        margin:13px;
    }
    .second>span{
        margin:10px 0;
    }
    .second>span>span{
        color:#ff7600;
    }
    .second button{
        width:100%;
        height:45px;
        margin:5px 0;
        border-radius:0;
    }
    .second button:hover{
        cursor:pointer;
    }
    .second input{
        height:45px;
        border-radius:0;
        font-size:12px;
        width:62%;
    }
    .second button:nth-child(4){
        background:#ff7600;
    }
    .second button:nth-child(4):hover{
        color:#fff
    }
    .second input+button{
        width:35%;
        margin:0  0 0 10px;
        color:#000;
    }
    .second input+button:hover,.second button:nth-child(5):hover{
        color:#fff;
        background:#ff7600;
    }
/* */
    .third{
        display:flex;
        justify-content:flex-start;
        flex-direction:column;
    }
    .third input{
        width:350px;
        margin:0 0 10px 0;
        border-radius:0;
        font-size:12px;
    }
     .third button{
         width:350px;
         height:45px;
         margin:20px 0;
         background:#ff7600;
         color:#fff;
         font-size:15px;
     }
</style>