<template>
    <div class="detial">
        <mt-nav class="detial_nav"></mt-nav>
        <div class="detial_list" @mouseleave="leavelist">
            <div class="zhaozi" @mouseenter="enterlist"></div>
            <mt-left class="detial_list_in" v-show="display_list"></mt-left>
        </div>
        
        
        <!--  -->
        <div class="buybody">
            <div class="fixed-out" :style="fixed">
                <div class="fixed">
                    <div class="fix-name">
                        <span></span>
                        <span>Redmi7</span>
                    </div>
                    <div class="fix-cla">
                        <span></span>
                        <span></span>
                        <span>F</span>
                        <span></span>
                    </div>
                </div>
            </div>
        </div>
        <el-container>
            <el-aside width="600px">
                <img :src="buyimg" alt="" :style="fixed0">
            </el-aside>

            <el-main>
                <p>9</p>
                <p> / 855 / 4800</p>
                <p>3299</p>
                <div>
                    <span></span> <span></span>
                </div>
                <div class="sure" v-if="ff===0">
                    <i class="el-icon-location-outline"></i>
                    <span>{{add1}}</span>
                    <span>{{add2}}</span>
                    <span>{{add3}}</span>
                    <p @click="gai"></p>  
                </div>
                <div class="address" v-else="ff===1">
                    <div>
                        <span v-for="(p,i) of xz" @click="mm(i)">{{p}}</span>
                        <span class="yello" v-if="nn===0">/</span>
                        <span class="yello" v-else-if="nn===1">/</span>
                        <span class="yello" v-else-if="nn===2"></span>
                        <span v-else="nn===3"></span>
                        <p @click="ol"></p>
                    </div>

                    <div>
                        <div v-if="ceng===0">
                            <span v-for="(s,i) of sheng" @click="fn(i,s)">{{s}}</span>
                        </div>
                        <div v-else-if="ceng===1">
                            <span v-for='(t,i) of tihuan' @click="ft(t,i)">{{t}}</span>
                        </div>
                        <div v-else-if="ceng===2">
                            <span v-for="(q,i) of tihuan" @click="fq(q)">{{q}}</span>
                        </div>
                        <div v-else="ceng==3">
                        </div>
                    </div>

                </div>
                <div class="guige">
                    <p></p>
                    <div>
                        <span v-for="(b,i) of banben" :key="i" @click="changeban(i,b)" :style="{border:'1px solid '+b.border}">{{b.size}}<span>{{b.price}}</span></span>
                       
                    </div>
                    <p></p>
                    <div>
                        <span v-for="(c,i) of color0" :key="i" :style="{border:'1px solid '+c.border}" @click="changeimg(i,c)"><span :style="{background:c.color}"></span>{{c.zi}}</span>
                    </div>
                    <p></p>
                    <div class="baoxian">
                        <input type="checkbox"  v-model="chek0" @click="changecheks">
                        <img src="img/buy/pms_1550560290.8631216.jpg" alt="">
                        <div>
                            <span><input type="checkbox" v-model="chek1" @click="weixiu0"> <span>299</span></span>
                            <span><input type="checkbox" v-model="chek2" @click="weixiu1">// <span>159</span></span>
                            <span><input type="checkbox" v-model="chek3" @click="weixiu2">, <span>99</span></span>
                        </div>
                    </div>
                    <div class="total">
                        <span v-for="(h,i) of huizong" :key="i">
                            <span>{{h.name}}</span>
                            <span>{{h.size}}</span>
                            <span>{{h.price}}</span>
                        </span>
                        <div>: <span>{{total}}</span> </div>
                    </div>
                    <div class="tocart">
                            <span @click="addcart"></span>
                    </div>
                    <div>
                        <i class="el-icon-success"></i>
                        <i class="el-icon-success"></i>
                        <i class="el-icon-success">7</i>
                        <i class="el-icon-success"></i>
                    </div>
                </div>
            </el-main>
        </el-container>
    <mt-footer></mt-footer>
    </div>
</template>
<script>
import mtNav from '../components/navigation.vue'
import mtLeft from '../components/left.vue'
import mtFooter from '../components/footer.vue'
export default {
    data(){
        return{
           display_list:false,
           fixed0:"",
           huizong:[
               {name:"9 ",size:"8GB+128GB",price:"1999"},
           ],
            total:1999,
            chek0:false,
            chek1:false,
            chek2:false,
            chek3:false,
            fixed:'',
           color0:[],
           buyimg:"img/buy/pms_1550642242.36585125.jpg",
           color1:[{color:"purple",zi:"",border:"#ff7600",img:"img/buy/pms_1550642242.36585125.jpg"},{color:"#45f",zi:"",border:"#ccc",img:"img/buy/pms_1550642240.48638886.jpg"},{color:"gray",zi:"",border:"#ccc",img:"img/buy/pms_1550642238.19945420.jpg"}],
           color2:[{color:"black",zi:"",border:"#ff7600",img:'img/buy/pms_1553001921.97126644.jpg'}],
           banben:[{size:"8GB+128GB",price:"3299",border:"#ff7600",img:"img/buy/pms_1550642242.36585125.jpg"},{size:"8GB+256GB",price:"3699",border:"#ccc",img:'img/buy/pms_1553001921.97126644.jpg'},{size:"6GB+128GB",price:"2399",border:"#ccc",img:"img/buy/pms_1550642242.36585125.jpg"}],
           xz:[],
           sheng:["","","","","","",""],
           tihuan:[],
           shi:[["","","","","","",""],["","","","","","","",""],["","","","","","","",""],["","","",""],["","","","","",""],["","","","","","","",""],["","","","",""]],
           qu:[
               [["","",""],["","",""],["","",""],["",""],["",""],["",""],["",""]],
               [["",""],["",""],["",""],["",""],["",""],["",""],["",""],["",""],["",""]],
               [["","","",""],["",""],["","",""],["","",""],["",""],["",""],["",""],["",""]],
               [["",""],["",""],["",""],["",""]],
               [["",""],["",""],["",""],["",""],["",""],["",""]],
               [["",""],["",""],["",""],["",""],["",""],["",""],["",""],["",""]],
               [["",""],["",""],["",""],["",""],["","",""]]
               ],
           ceng:0, //
           nn:0,  ////
           n:0, //
           m:0, //
           add1:'',
           add2:'',
           add3:'',
           ff:0, //
        }
    },
    watch:{
        huizong(){
            var pp=0
            for(var key of this.huizong){
                pp+=parseInt(key.price)
            }
            this.total=pp
        },
        chek1(){
            
            if(this.chek2==true && this.chek3==true){
                this.chek0=true
            }
            if(this.chek1==false){
                this.chek0=false 
            }      
        },
        chek2(){
            if(this.chek1==true && this.chek3==true){
                this.chek0=true
            }
            if(this.chek2==false){
                this.chek0=false 
            }     
        },
        chek3(){
            if(this.chek2==true && this.chek1==true){
                this.chek0=true
            }
            if(this.chek3==false){
                this.chek0=false 
            }     
        }
    },
    created(){
        this.color0=this.color1
        window.addEventListener('scroll',this.handleScroll) //
    },
    methods:{
        addcart(){
           if(this.$store.getters.getUid){
            var pname=this.huizong[0].name
            var price=this.huizong[0].price
            var img=this.buyimg
            var uid=this.$store.getters.getUid
            var url='addcart'
            var formdata='pname='+pname+'&price='+price+'&img='+img+'&uid='+uid
                this.axios.post(url,formdata).then(res=>{
                    if(res.data.code){
                         this.$message({
                            message: '',
                            type: 'success',
                            duration:1000
                        })
                    }
                })
                setTimeout(()=>{
                    this.$router.push('/shopcart')
                },2000)
            }else{
                this.$message({
                message:'',
                type:'info'
            })
            }
        },
        weixiu0(){
            if(this.chek1==false){
                this.huizong.push({name:"",size:"",price:"299",num:1})
            }else{
                for(var key of this.huizong){
                    if(key.num==1){
                        this.huizong.splice(this.huizong.indexOf(key),1)
                    }
                }
            }
        },
        weixiu1(){
            if(this.chek2==false){
                this.huizong.push({name:"",size:"",price:"159",num:2})
            }else{
                for(var key of this.huizong){
                    if(key.num==2){
                        this.huizong.splice(this.huizong.indexOf(key),1)
                    }
                }
            }
            console.log(this.huizong)
        },
        weixiu2(){
            if(this.chek3==false){
                this.huizong.push({name:"",size:"",price:"99",num:3})
            }else{
                for(var key of this.huizong){
                    if(key.num==3){
                        this.huizong.splice(this.huizong.indexOf(key),1)
                    }
                }
            }
        },
        changecheks(){ 
            if(this.chek0==true){
                this.huizong.splice(1)
                this.chek1=false
                this.chek2=false
                this.chek3=false
            }else{
                this.huizong.push({name:"",size:"",price:"299",num:1},{name:"",size:"",price:"159",num:2},{name:"",size:"",price:"99",num:3})
                this.chek1=true
                this.chek2=true
                this.chek3=true
            }
        },
        changeimg(i,c){
            for(var key of this.color0){
                key.border="#ccc"
            }
            this.color0[i].border="#ff7600"
            this.buyimg=this.color0[i].img
            this.huizong[0].name="9 "+c.zi
        },
        changeban(i,b){
           
             this.huizong[0].size=b.size+""
             this.huizong[0].price=b.price
             for(var key of this.color0){
                key.border="#ccc"
            }
            this.color0[0].border="#ff7600"
            
            if(i==0){
                this.color0=this.color1
            }else if(i==1){
                this.color0=this.color2
            }else{
                this.color0=this.color1
            }
            for(var key of this.banben){
                    key.border="#ccc"
                }
                this.banben[i].border="#ff7600" 
                this.buyimg=this.banben[i].img 
        },
        fn(i,s){
            this.add1=s
            this.n=i
            this.nn=1
            this.ceng=1
           this.tihuan=this.shi[i]
           this.xz.push(s)
        },
        ft(t,i){
            this.add2=t
            this.m=i  
            this.nn=2
            this.ceng=2
           this.tihuan=this.qu[this.n][i]
           this.xz.push(t)
        },
        fq(q){
            this.add3=q
            this.nn=3
            this.ceng=3
            this.tihuan=[]
            this.xz.push(q)
        },
        mm(i){ 
            this.nn=i
            this.xz.splice(i)
            this.ceng=i
            if(i==1){
                this.tihuan=this.shi[this.n] 
            }else if(i==2){ 
                this.tihuan=this.qu[this.n][this.m]     
            }                
        },
        gai(){//
            this.ff=1
        },
       ol(){  
            if(this.add1=="" || this.add1==""){
                this.$notify({
                    title: '',
                    message: '',
                    type: 'warning'
                });
            }else if(this.add2=="" || this.add2==""){
                this.$notify({
                    title: '',
                    message: '',
                    type: 'warning'
                 })
            }else if(this.add3=="" || this.add3==""){
                this.$notify({
                title: '',
                message: '',
                type: 'warning'
                })
            }else{
                this.ff=0
            }
        
      },
       enterlist(){
           this.display_list=true
       },
       leavelist(){
           this.display_list=false
       },
       handleScroll () {          
        var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;  //
        if(scrollTop>200 && scrollTop<700){
            this.fixed0="margin-top:"+(scrollTop-200)+"px"
        }
        if(scrollTop>200){
            this.fixed="position:fixed;top:50px"
        }else{
            this.fixed=""    
        }
        if(scrollTop>2300 && scrollTop<2500){
            this.change_size="transform:scale(2.5)"
        }
       
       }
    },

    components:{
        mtNav,
        mtLeft,
        mtFooter
    }
}
</script>
